<script>
import http from '@/libs/http'
import {URL} from '@/libs/url'
import echarts from 'echarts'
import EchartsCoponent from '../../components/echart'
import Swiper from 'swiper';
export default {
  name: 'appSiredData',
  data () {
    return {
      title:'APP',
      pickerOptions: {
        shortcuts: [{
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      echartDefaultDate: [],//
      ZechartDefaultDate:[],//
      detailDefaultDate:[],//
      tableData: [],//
      partData:'',//
      loading: true,
      IDNAME:'echart1',//id
      IDNAMEZ:'echart1_1',//id
      Valuename:'',//
      ValuenameZ:'',//
      btnType:1,// 1- 2- 3- 
      downloadExcelUrl:'',//
      PageName:'0',//  0- 2- 1-Android  ()
      PageNameType:'1',//
      MiddleType:'',// 1- 2- 3- 4- 
      //
      ChildShowTypeAll:'1',//1-3   
      //
      ChildShowTypeZ:'1',//1-3   
      ChildTypeZ:'1',//middletype =1  1-3   
                  //middletype =2  1-7      - 
                  //middletype =3 1 
                  //middletype =4 1-3  3 
      //
      ChildShowTypeBox:'1',//1-3   
      childconfig:[],
      options: [],
      id:'',//id
      childId:'',//id
      MiddleList:[],
      tableDataHeader:[],
    }
  },
  created(){
    document.title = this.title;
  },
  activated (){
    document.title = this.title;
  },
  mounted() {
    this.bindDrawal();//
  },
  methods: {
    //
    bindDrawal(){
      this.getChildConfig();
      this.getConfig();
      this.echartDefaultDate.push(this.common.formatDate(-6),this.common.formatDate(0))
      this.ZechartDefaultDate.push(this.common.formatDate(-6),this.common.formatDate(0))
      this.detailDefaultDate.push(this.common.formatDate(-6),this.common.formatDate(0))
    },
    swiper(){
      new Swiper('.swiper-container', {
        nextButton: '.swiper-button-next',
        prevButton: '.swiper-button-prev',
        slidesPerView: "auto",
        paginationClickable: true,
        freeMode : false,
        freeModeMomentum : true,
        observer: true,//swiperswiper
        observeParents: true,//swiperswiper
    });
    },
    //
    getConfig(){
      var that = this;
      http({
        //urldata
        method: 'get',
        url:URL.recordUrl.getConfig,
        data: {},
        //
        headers: {
          "Content-Type":"application/x-www-form-urlencoded",
        }
      }).then(function (res) {
        if(res.code==0){
         var optionsi = res.data;
          var obj = {
            label:'',
            value:0,
          };
          optionsi.unshift(obj)
          that.options = optionsi;
          
        }
      }).catch(function (err) {
        console.log(err);
      });
    },
    //
    getChildConfig(){
      var that = this;
      http({
        //urldata
        method: 'get',
        url:URL.recordUrl.getChildConfig+"?Type="+that.PageName,
        data: {},
        //
        headers: {
          "Content-Type":"application/x-www-form-urlencoded",
        }
      }).then(function (res) {
        if(res.code==0){
          that.childconfig = res.data;
          that.MiddleType = res.data[0].value;
          that.getPartDataAll();
          that.getChildEChartConfig();
          that.getDetails();
        }else if(res.code==30005){
          that.$message(res.message);
        }
      }).catch(function (err) {
        console.log(err);
      });
    },
    getChildEChartConfig(){
      console.log(this.MiddleType);
      var that = this;
      http({
        //urldata
        method: 'get',
        url:URL.recordUrl.getChildEChartConfig+"?Type="+that.PageName+"&id="+that.MiddleType,
        data: {},
        //
        headers: {
          "Content-Type":"application/x-www-form-urlencoded",
        }
      }).then(function (res) {
        that.MiddleList = res.data;
        that.ChildTypeZ = res.data[0].value;
        that.getPartData();
        that.swiper();
      }).catch(function (err) {
        console.log(err);
      });
    },
    PageNameBtn() {
      console.log("",this.PageName);
      this.tableData = [];
      this.getDetails();//
      this.getPartDataAll();//
      this.getPartData();//
    },
    changetabPosition (){
      console.log(this.MiddleType);
      this.tableData = [];
      this.getDetails();//
      this.getPartDataAll();//
      this.getPartData();//
      this.getChildEChartConfig();
    },
    //
    ChildShowTypeAllBtn() {
      if(this.ChildShowTypeAll==1){
        this.Valuename = ''
        this.IDNAME = 'echart1'
        this.btnType = 1
      }else if(this.ChildShowTypeAll==2){
        this.Valuename = ''
        this.IDNAME = 'echart2'
        this.btnType = 1
      }else if(this.ChildShowTypeAll==3){
        this.Valuename = ''
        this.IDNAME = 'echart3'
        this.btnType = 1
      }
      this.getPartDataAll();
    },
    //1
    ChildTypeZBtn() {
      console.log("MiddleType",this.MiddleType);
      console.log("ChildTypeZ",this.ChildTypeZ);
      this.getPartData();
    },
    //
    ChildShowTypeZBtn() {
      this.getPartData();
    },
    //
    changeEchartTimedetail(){
      this.tableData = [];
      this.getDetails();
    },
    //
    getPartDataAll (){
      var that = this;
      http({
        //urldata
        method: 'get',
        url:URL.recordUrl.getChildEChartsData+"?Type="+that.PageName+"&StartTime="+that.echartDefaultDate[0]+"&endtime="+that.echartDefaultDate[1]+'&ChildShowType='+that.ChildShowTypeAll+'&id='+that.MiddleType,
        data: {},
        //
        headers: {
          "Content-Type":"application/x-www-form-urlencoded",
        }
      }).then(function (res) {
        that.loading = false;
        if(res.code==0){
          that.partData = res.data;
          console.log("",that.partData);
          var datelist = [];
          var dataValueList = [];
          that.partData.forEach((item,index)=>{
            var datestr = item.dateStr;
            var datestr1 = datestr.split("");
            datestr1.splice(4,0,"-");
            datestr1.splice(7,0,"-");
            datestr1 = datestr1.join("");
            dataValueList.push(item.value*1);
            datelist.push(datestr1);
          });
          console.log("datelist",datelist);
          console.log("dataValueList",dataValueList);
          that.showechart(dataValueList,datelist,that.Valuename,that.IDNAME,that.btnType);
        }
      }).catch(function (err) {
        console.log(err);
      });
    },
    //
    getPartData (){
      var that = this;
      http({
        //urldata
        method: 'get',
        url:URL.recordUrl.getAppGoldChildEChartData+"?Type="+that.PageName+"&startTime="+that.echartDefaultDate[0]+"&endtime="+that.echartDefaultDate[1]+'&ChildShowType='+that.ChildShowTypeZ+'&Childid='+that.ChildTypeZ,
        data: {},
        //
        headers: {
          "Content-Type":"application/x-www-form-urlencoded",
        }
      }).then(function (res) {
        that.loading = false;
        if(res.code==0){
          that.partData = res.data;
          console.log("",that.partData);
          var datelist = [];
          var dataValueList = [];
          that.partData.forEach((item,index)=>{
            var datestr = item.dateStr;
            var datestr1 = datestr.split("");
            datestr1.splice(4,0,"-");
            datestr1.splice(6,0,"");
            datestr1 = datestr1.join("");
            dataValueList.push(item.value*1);
            datelist.push(datestr1);
          });
          console.log("datelist",datelist);
          console.log("dataValueList",dataValueList);
          that.showechart(dataValueList,datelist,that.ValuenameZ,that.IDNAMEZ,that.btnType);
        }
      }).catch(function (err) {
        console.log(err);
      });
    },
    //
    getDetails (){
      var that = this;
      http({
        //urldata
        method: 'get',
        url:URL.recordUrl.getAppGoldChildDetail+"?Type="+that.PageName+"&startTime="+that.detailDefaultDate[0]+"&endtime="+that.detailDefaultDate[1]+'&ChildShowType='+that.ChildShowTypeBox+'&id='+that.MiddleType,
        data: {},
        //
        headers: {
          "Content-Type":"application/x-www-form-urlencoded",
        }
      }).then(function (res) {
        that.loading = false;
        that.tableDataHeader = res.data[0];
        for(var i=0;i<res.data.length;i++){
          var tablist;
          if(i !== 0){
            tablist = res.data[i];
          }
          that.tableData.push(tablist);
        }
         // if(that.tableData.length==0){
          //   that.tableData.push(tablist);
          // }else{
          //   that.tableData.splice(1,that.tableData.length-1,tablist);
          // }
        that.downloadDetailExcelUrl();
        console.log("",that.tableData);

      }).catch(function (err) {
        console.log(err);
      });
    },
    //
    ChildShowTypeBoxBtn() {
      this.tableData = [];
      this.getDetails();
    },
    //
    downloadDetailExcelUrl(){
      this.downloadExcelUrl =  URL.recordUrl.getAppGoldChildDetailExport+"?startTime="+this.detailDefaultDate[0]+"&endtime="+this.detailDefaultDate[1]+'&Type='+this.PageName+'&ChildShowType='+this.ChildShowTypeBox+'&MiddleType='+this.MiddleType;
    },
    changeEchartTime() {
      console.log(this.echartDefaultDate);
      this.getPartDataAll()
    },
    //
    ZchangeEchartTime() {
      console.log(this.ZechartDefaultDate);
      this.getPartData();
    },
    //
    showechart(dataValueList,datelist,Valuename,idName,btnType){
      var myChart = echarts.init(document.getElementById(idName));
      //(*2)
      //btnType1- 2- 3- 
      var ValueArr = [];//
      var max;//
      var min;//
      var tooltip;
      var yaxisLabel;
      var that = this;
      if(btnType==2){
        dataValueList.forEach(function(data){
           ValueArr.push(data);
      })
        max=Math.round(Math.max.apply(null,ValueArr)*1.2);
        tooltip = {
          trigger: 'axis',
          //
          formatter: function (params) {
            var $result = '00:00';
            if (params[0].value>0){
                var $hour = Math.floor(params[0].value/3600);
                var $minute = Math.floor(params[0].value%3600/60);
                var $second = Math.floor((params[0].value-60 * $minute) % 60);
                if ($hour < 10) {
                    $hour = '0'+$hour;
                }
                if($minute<10){
                    $minute = "0"+$minute;
                }
                if($second<10){
                    $second = "0"+$second;
                }
                $result = $hour+':'+$minute+':'+$second;
            }else{
              $result = "00"+':'+"00"+':'+"00";
            }
            var htmlStr = '';
            htmlStr += '<div><span style="color:#fff;">' + params[0].name + '</span><br/> ';
            for(var i=0;i<params.length;i++){
            //
              htmlStr += '<span style="width: 8px;height: 8px;margin-right:5px;display:inline-block;border-radius: 50%;background-color:'+params[i].color+'"></span><span style="color:#fff;">' + params[i].seriesName + ':</span>'+
                '<span style="color:#fff;">' + $result + '</span><br/>';
            }
            htmlStr += '</div>';
            return  htmlStr
          },
        }
        yaxisLabel = {
          textStyle: {
            color: '#666',
          },
          formatter:function ($times) {
            var $result = '00:00';
              if ($times>0){
                  var $hour = Math.floor($times/3600);
                  var $minute = Math.floor($times%3600/60);
                  var $second = Math.floor(($times-60 * $minute) % 60);
                  if ($hour < 10) {
                      $hour = '0'+$hour;
                  }
                  if($minute<10){
                      $minute = "0"+$minute;
                  }
                  if($second<10){
                      $second = "0"+$second;
                  }
                  return $result = $hour+':'+$minute+':'+$second;
              }else{
                return $result = "00"+':'+"00"+':'+"00";
              }
            }
          }
      }else if(btnType==3){
        dataValueList.forEach(function(data){
      ValueArr.push(data);
      })
        max=Math.round(Math.max.apply(null,ValueArr)*1.2);
      }else if(btnType==1){
        //dataValueList*1.2Y
        dataValueList.forEach(function(data){
           ValueArr.push(data);
      })
        max=Math.round(Math.max.apply(null,ValueArr)*1.2);
        tooltip = {
          trigger: 'axis',
        }
        yaxisLabel = {
          textStyle: {
            color: '#666'
          }
        }
      }
      
      myChart.setOption({
        // title: {
        //     text: Valuename+''
        // },
        tooltip: tooltip,
        legend: {
            data: []
        },
        grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
        },
        toolbox: {
            feature: {
                saveAsImage: {}
            }
        },
        xAxis: {
            type: 'category',
            boundaryGap: false,
            data: datelist,
            splitLine:{show: false},//
            axisLine: {
              lineStyle: {
                type:'solid',
                color:'#E6E6E6',//
                width:'2'//
              }
            },
            axisLabel: {
              textStyle: {
                color:'#666',//

              }
            }
        },
        yAxis: {
            type: 'value',
            min: min,
            max: max,
            splitLine:{
              lineStyle: {
                type:'dashed',
              }
            },
            axisLine: {
              lineStyle: {
                  type: 'solid',
                  color:'#E6E6E6',
                  width:'2'
              }
            },
            axisLabel: yaxisLabel
        },
        series: [
            {
                name: Valuename,
                type: 'line',
                stack: '',
                smooth: true,
                itemStyle : {  
                  normal : {  
                    color:'#396FFF',  
                    lineStyle:{  
                      color:'#396FFF'  
                    }  
                  }  
                },  
                data: ValueArr
            },
        ]
      });
    }
  }
}
</script>

